.header {
  grid-column: 1/3;
  grid-row: 1/2;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  background-color: #fdfcfa;
  box-shadow: 0rem 0rem 0.5rem rgba(#000000, 0.5);
  &__title {
    grid-column: 2/3;
    align-self: center;
    justify-self: center;
    font-family: "Kaushan Script", cursive;
    font-size: 1.8rem;
    color: #d01339;
  }
  p {
    color: grey;
  }
  &__profile {
    display: flex;
    flex-direction: column;
    grid-column: 3/4;
    margin-right: 1rem;
    margin-top: 0.5rem;
    align-items: flex-end;
    img {
      height: auto;
      width: 4rem;
      border-radius: 4rem;
      margin-right: 1rem;
    }
  }
}
